.circle-in-text {
    width: 50px;
    height: 50px;
    line-height: 50px;
    border-radius: 40%;
    text-align: center;
    border: 1px solid rgb(26, 1, 255);

  }

  .scrollmenu {
    overflow: auto;
    white-space: nowrap;
  }
  
  .scrollmenu-item{
